import { connect } from 'react-redux';
import FilmPage from './FilmPage';
import fetchFilms from '../../redux/filmsSearch/filmsSearchOperations';
import {
  addFilmToFavoritesActionCreator,
  markAsWatchedActionCreator,
  deleteFromFavorites,
  deleteFromWatched,
} from '../../redux/userList/userListActions';

const mapStateToProps = state => ({
  films: state.films,
  favoritesList: state.favoritesList,
  watchedList: state.watchedList,
});
const mapDispatchToProps = dispatch => ({
  fetchFilms: (query, page) => dispatch(fetchFilms(query, page)),
  addFilmToFavorites: film => dispatch(addFilmToFavoritesActionCreator(film)),
  markAsWatched: film => dispatch(markAsWatchedActionCreator(film)),
  deleteFromFavorites: id => dispatch(deleteFromFavorites(id)),
  deleteFromWatched: id => dispatch(deleteFromWatched(id)),
});
export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(FilmPage);
