import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import axios from 'axios';
import { API_KEY } from '../../redux/filmsSearch/filmsSearchOperations';

const FilmPage = ({
  films,
  match,
  addFilmToFavorites,
  markAsWatched,
  favoritesList,
  watchedList,
  deleteFromFavorites,
  deleteFromWatched,
}) => {
  const [chosenFilm, setChosenFilm] = useState('');
  useEffect(
    () =>
      setChosenFilm(films && films.find(film => film.id === match.params.id)),
    // eslint-disable-next-line
    [match.params.id],
  );
  if (!chosenFilm) {
    axios
      .get(
        `https://api.themoviedb.org/3/search/movie?api_key=${API_KEY}&query=${match.params.title}&include_adult=false`,
      )
      .then(response => {
        const filmsResult = response.data.results;
        setChosenFilm(
          filmsResult.find(film => film.id === Number(match.params.id)),
        );
      })
      .catch(error => console.log(error));
  }

  const addToFavorites = () => {
    addFilmToFavorites(chosenFilm);
  };
  const addToWatched = () => {
    markAsWatched(chosenFilm);
  };
  const deleteChosenFilmFromFavorites = () => {
    deleteFromFavorites(chosenFilm.id);
  };
  const deleteChosenFilmFromWatched = () => {
    deleteFromWatched(chosenFilm.id);
  };
  return (
    <section>
      {' '}
      {chosenFilm && (
        <>
          <h2> {chosenFilm.title} </h2>{' '}
          <img
            src={
              chosenFilm.poster_path
                ? `https://image.tmdb.org/t/p/w200/${chosenFilm.poster_path}`
                : 'https://www.viser.edu.rs/images/no_photo.jpeg'
            }
            alt="film poster"
          />
          <p> {chosenFilm.vote_average || 'No votes'} </p>{' '}
          <p> {chosenFilm.overview || 'No overview available'} </p>{' '}
          {favoritesList.find(
            film => Number(film.id) === Number(chosenFilm.id),
          ) ? (
            <button type="button" onClick={deleteChosenFilmFromFavorites}>
              Delete from fav{' '}
            </button>
          ) : (
            <button type="button" onClick={addToFavorites}>
              Add to favg{' '}
            </button>
          )}{' '}
          {watchedList.find(
            film => Number(film.id) === Number(chosenFilm.id),
          ) ? (
            <button type="button" onClick={deleteChosenFilmFromWatched}>
              Delete from watched{' '}
            </button>
          ) : (
            <button type="button" onClick={addToWatched}>
              Add to watched{' '}
            </button>
          )}{' '}
        </>
      )}{' '}
    </section>
  );
};
FilmPage.propTypes = {
  films: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  addFilmToFavorites: PropTypes.func.isRequired,
  markAsWatched: PropTypes.func.isRequired,
  match: PropTypes.shape({
    params: PropTypes.shape({
      title: PropTypes.string.isRequired,
      id: PropTypes.string.isRequired,
    }),
  }).isRequired,
  favoritesList: PropTypes.arrayOf().isRequired,
  watchedList: PropTypes.arrayOf().isRequired,
  deleteFromFavorites: PropTypes.func.isRequired,
  deleteFromWatched: PropTypes.func.isRequired,
};
export default FilmPage;
